import React, {useState} from 'react'

import { CInput } from 'c-input'
import 'c-input/dist/index.css'

const App = () => {
  const [value, setValue] = useState()
  const handleChange = e => {
    setValue(e.target.value)
  }
  return (
    <>
      <CInput 
        classes="border  h-8 mt-1 focus:ring-indigo-500 focus:border-indigo-500 block w-full shadow-sm sm:text-sm border-gray-300 rounded-md"  
        name='myInput'
        type='text'
        value={value}
        placeholder="my input placeholder"
        onChange={handleChange}
        fullWidth={true}
      />
    </>
  )
}

export default App
