import React from 'react'
import "../styles/tailwind.css"

export const CInput = props => {
  const {
    autoComplete, 
    autoFocus, 
    classes, 
    defaultValue,
    disabled,
    fullWidth,
    id,
    name,
    onChange,
    placeholder,
    type,
    value
  } = props
  return (
    <input 
      autoComplete={autoComplete}
      autoFocus={autoFocus}
      className={classes}
      defaultValue={defaultValue}
      disabled={disabled}
      fullWidth={fullWidth}
      id={id}
      name={name}
      onChange={onChange}
      placeholder={placeholder}
      type={type}
      value={value}
    />
  )
}
